import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams, ViewController} from 'ionic-angular';
import {AppConfigProvider} from '../../providers/appconfig/appconfig';
import {ContentProvider} from '../../providers/content/content';
import {ImageViewerController} from 'ionic-img-viewer';

/**
 * Generated class for the PersonDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-person-detail',
  templateUrl: 'person-detail.html',
})
export class PersonDetailPage {

  user: any;
  person: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public acp: AppConfigProvider,
    public modalCtr: ModalController,
    public cntProvider: ContentProvider,
    public viewCtrl: ViewController,
    public imageViewerCtrl: ImageViewerController) {
    this.person = this.navParams.get('personData');
    this.user = this.acp.getUser();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PersonDetailPage');
  }

  onClick(imageToView) {
    const viewer = this.imageViewerCtrl.create(imageToView);
    viewer.present();
  }

  edit(): void {
    const modal = this.modalCtr.create('PersonCreatorPage', {person: this.person});
    modal.onDidDismiss(updatedData => {
      if (updatedData) {
        if (updatedData.delete === true) {
          this.cntProvider.delPerson(updatedData);
          this.viewCtrl.dismiss({refresh: true});
        } else {
          this.person = updatedData;
          this.cntProvider.updatePerson(updatedData);
        }
      }
    });
    modal.present();
  }

  close(): void {
    this.viewCtrl.dismiss();
  }

}



